.comment-input {
    background-color: white;
    border: 1px solid #f1f1f1;
    padding: 20px;
    margin-bottom: 10px;
  }
  
  .comment-field {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
  }
  
  .comment-field .comment-field-name {
    display: flex;
    flex-basis: 100px;
    font-size: 15px;
    font-weight: bold;
    color: rgb(97, 136, 85);
  }
  
  .comment-field .comment-field-input {
    display: flex;
    flex: 1;
  }
  
  .comment-field-input textarea {
    border: 1px solid #e6e6e6;
    border-radius: 3px;
    padding: 10px;
    outline: none;
    font-size: 14px;
    resize: none;
    flex: 1;
  }
  
  .comment-field-input textarea {
    height: 50px;
  }
  
  .comment-field-button {
    display: flex;
    justify-content: flex-end;
  }
  
  .comment-field-button button {
    padding: 6px 10px;
    width: 80px;
    border: none;
    border-radius: 3px;
    background-color: rgb(164, 197, 143);
    color: white;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .comment-field-button button:active {
    background: rgb(97, 136, 85);
  }