@import url(https://fonts.googleapis.com/css?family=Monoton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.vids_logo {
    width: 50px;
    margin-left: 30px;
}
.logo_container {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn_container {
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo_container label {
    padding-left: 10px;
    font-family: 'Monoton', cursive;   
    font-size: 1.6em;
}
.nav_tags {
    display: flex;
}

.newNav {
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 100;
}

.nav_bar {
    display: flex;
    justify-content: space-between;
    background-color: rgb(97, 136, 85);
    align-items: center;
    width: 100%;
    height: 60px;
    z-index: 100;
}

.navTag {
    color:white;
    text-decoration: none;
    margin: 15px;
    transition: left ease-in-out 200ms;
    height: 62px;
    padding-bottom: 15px;

}

.nav_tags {
    margin-right: 40px;
}

.nav_tags > li {
    list-style: none;
}

.login_btn {
    font-weight: bold;
    font-size: 13px;
    height: 30px;
    width: 60px;
    border-radius: 5px;
    border: none;
    margin-right: 20px;
}
.menuBtn {
    background: none;
    border: none;
    margin-right: 10px;
}

.toggleNavContainer {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}
.toggle_button_line {
    width: 40px;
    height: 4px;
    margin: 6px;
    background-color: white;

}



.toggleMenu {
    width: 20%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    transition: all ease-in-out 200ms;
    
    background: rgba(214, 212, 212, 0.7); 
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); 
    -webkit-backdrop-filter: blur(10px); 
            backdrop-filter: blur(10px); 
    z-index: 1000;
}
.toggle_nav_tags {
    margin: 0;
    width: 100%;
    text-align: center;
    list-style: none;
    padding: 30px 0;
}
.toggle_nav_tags > li {
    margin: 45px;
}
.toggle_navTag {
    color: rgb(65, 63, 63);
    padding: 5px;
    text-decoration: none;
}

.landingPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.searchBar {
    margin: 120px 0px 50px 0px;
}

.video {
    display: inline-block;
    font-size: 20px;                 /* restore font size removed in container */
    list-style-type: none;
    width: 330px;
    height: 150px;
    line-height: 20px;
    margin: 40px 30px;
    box-sizing: border-box;
    text-align: center;
}

.vid-row {
    margin: 0 auto;                  /* center container */
    width: 1200px;
    padding-left: 0;                 /* remove list padding */
    font-size: 0;
}
.vidTitle {
    color: black;
    text-decoration: none;
}
.vidTitle :hover{
    text-decoration: underline;
}


@media screen and (max-width: 430px) { .vid-row { width: 200px; } }
@media screen and (min-width: 431px) and (max-width: 630px) { .vid-row { width: 400px; } }
@media screen and (min-width: 631px) and (max-width: 830px) { .vid-row { width:600px;  } }
@media screen and (min-width: 831px) and (max-width: 1030px) { .vid-row { width: 800px; } }
@media screen and (min-width: 1031px) and (max-width: 1230px) { .vid-row { width: 1500px; } }
.searchContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.searchContainer form {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}
.searchContainer form input {
    padding-left: 20px;
    font-size: 15px;
    width: 600px;
    z-index:999;
    height: 30px;
    border-radius: 5px;
    border: lightgray solid 1px;
    box-shadow: rgba(0, 0, 0, 0.1) 0 0 0 0, rgba(0, 0, 0, 0.1) 0px 4px 11px;;
}
.searchContainer button {
    width: 30px;
    height:30px;
    background-image: url(data:image/png;base64,);
    border-style: none;
    margin-left: 10px;
}


.comment-input {
    background-color: white;
    border: 1px solid #f1f1f1;
    padding: 20px;
    margin-bottom: 10px;
  }
  
  .comment-field {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
  }
  
  .comment-field .comment-field-name {
    display: flex;
    flex-basis: 100px;
    font-size: 15px;
    font-weight: bold;
    color: rgb(97, 136, 85);
  }
  
  .comment-field .comment-field-input {
    display: flex;
    flex: 1 1;
  }
  
  .comment-field-input textarea {
    border: 1px solid #e6e6e6;
    border-radius: 3px;
    padding: 10px;
    outline: none;
    font-size: 14px;
    resize: none;
    flex: 1 1;
  }
  
  .comment-field-input textarea {
    height: 50px;
  }
  
  .comment-field-button {
    display: flex;
    justify-content: flex-end;
  }
  
  .comment-field-button button {
    padding: 6px 10px;
    width: 80px;
    border: none;
    border-radius: 3px;
    background-color: rgb(164, 197, 143);
    color: white;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .comment-field-button button:active {
    background: rgb(97, 136, 85);
  }
.comment {
    position: relative;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f1f1f1;
    margin-bottom: 10px;
    padding-bottom: 10px;
    min-height: 50px;
  }
  
  .comment .comment-user {
    flex-shrink: 0;
  }
  .comment-username {
    color: rgb(97, 136, 85);
    font-style: italic;
    font-weight: bold;
  }
.vidPlayPage {
    display: flex;
    justify-content:space-evenly;
    margin: 100px;
}
.vidNComments {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.vidPlayWindow {
    width: 800px;
    /* height: 500px;
    background-color: grey; */
}
.vidDescription {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 800px;
    min-height: 50px;
    border-bottom: 1px solid #f1f1f1;
}
.collectIcon {
    display: flex;
    align-items: center;
    margin-top: 15px;
}
.collectIcon img {
    margin-right: 5px;
    cursor: pointer;
}
.commentsArea {
    width: 760px;
    margin: 20px auto;
    font-size: 14px;
    background-color: white;
    border: 1px solid #f1f1f1;
    padding: 20px;
}
.vidsList {
    width: 300px;
    height: 550px;
    background-color: rgb(164, 197, 143);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
}
.vidsList video {
    margin: 10px;
}


.uploadPage {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.uploadBox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 130px 150px 20px;
    padding: 100px;
    border:  rgb(97, 136, 85) 2px solid;
    border-radius: 10px;
    background-image: url(data:image/png;base64,);
    height: 150px;
    background-repeat: no-repeat;
    background-position: center;
}
.uploadBox span {
    text-align: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
}

.uploadBtn{
    margin: 180px 150px 0px;
    padding: 6px 20px;
    height: 20px;
    line-height: 20px;
    position: relative;
    border: 1px solid #999;
    border-radius: 5px;
    text-decoration: none;
    color: #666;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}
.uploadBtn:hover {
    background-color: rgb(97, 136, 85);
    color: white;
}
.change{
    position: absolute;
    overflow: hidden;
    left: -250px;
    bottom: -36px;
    padding: 160px 200px;
    opacity: 0;
    border:  rgb(97, 136, 85) 2px solid;
    border-radius: 10px;
}
#showFileName {
    margin-top: 10px;
}
.uploadPage label {
    align-self: flex-start;
    margin-left: 400px;
    margin-top: 10px;
    font-weight: bold;
    font-size: larger;
}
.descriptionBox {
    width: 42%;
    height: 50px;
    margin-top: 15px;
    border-radius: 10px;
    border:  rgb(97, 136, 85) 2px solid;
    padding: 15px;
    resize: none;
    font-size: 15px;
}
.titleBox {
    width: 42%;
    height: 10px;
    margin-top: 15px;
    border-radius: 10px;
    border:  rgb(97, 136, 85) 2px solid;
    padding: 15px;
    resize: none;
    font-size: 15px;
}

.descriptionUpload {
    align-self: flex-end;
    margin-top: 20px;
    margin-right: 400px;
    padding: 6px 20px;
    height: 35px;
    line-height: 20px;
    border: 1px solid #999;
    border-radius: 5px;
    text-decoration: none;
    color: #666;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}
.descriptionUpload:hover {
    background-color: rgb(97, 136, 85);
    color: white;
}

.inputField {
    margin: 15px;
    width: 35%;
}
.input {
    width: 100%;
    height: 28px;
    border-radius: 5px;
    border: none;
    margin-left: 0px;
